<template>
  <v-ons-page>
    <v-ons-toolbar modifier="transparent"></v-ons-toolbar>
    <div class="header">
      <img src="../assets/logo.png" alt="Little">
    </div>

    <div style="text-align: center; padding-left: 20px; padding-right: 20px;">
      <h1>Welcome to Little</h1>
      <p>Enter Phone number to request <br>a Little Cab</p>
    </div>

    <v-ons-button @click="actionSheetVisible = true">
      Static action sheet
    </v-ons-button>
    <v-ons-list>
      <v-ons-list-item style="padding-right: 14px;">
        <div class="center" style="width: 100%;">
          <phone-input :code="getCountry" :availableOnly="['rw', 'ke', 'gh', 'ng']" :phone="phone"/>
        </div>
      </v-ons-list-item>
      <v-ons-list-item style="padding-right: 14px;">
        <div class="center">
          <v-ons-button @click="goHome" modifier="large">Proceed</v-ons-button>
        </div>
      </v-ons-list-item>
    </v-ons-list>
    <div style="text-align: center; margin-top: 30px;">
      <small>Tems and service stuff</small>
    </div>

    <v-ons-action-sheet
      :visible.sync="actionSheetVisible"
      cancelable
      title="Select Country"
    >
      <v-ons-action-sheet-button icon="md-square-o">Label 1</v-ons-action-sheet-button>
      <v-ons-action-sheet-button icon="md-square-o" modifier="destructive">Label 2</v-ons-action-sheet-button>
      <v-ons-action-sheet-button icon="md-square-o">Label 3</v-ons-action-sheet-button>
    </v-ons-action-sheet>



  </v-ons-page>
</template>

<script>
  export default {
        name: "login",
        data: function() {
          return {
            actionSheetVisible: false,
            countryCode: "",
            loading: false,
            phone: {
              code: '',
              number: ''
            }
          }
        },
      methods: {
          goHome: function () {
            const _self = this;
            this.$ons.notification.prompt("Please enter verification code sent via SMS on " + this.phone.number, {
              placeholder: "Code",
              title: "Phone Verification",
              cancelable: true
            }).then(function (data) {
              if(data === "1234"){
                return _self.$router.push('Home');
              } else {
                _self.$ons.notification.toast('Wrong verification code', {timeout: 2000})
              }
            });
          },
        changeCountry: function () {
          this.countryCode = "ng";
        }
      },
    computed: {
          getCountry: function () {
            const a = this.$store.getters['user/getIPLocation'];
            console.log(a.country ? a.country.toLowerCase() : 'ke');
            return a.country ? a.country.toLowerCase() : '';
          }
      }
    }
</script>

<style scoped>
  .header {
    display: grid;
    margin-bottom: 40px;
  }
  .header img{
    margin: 0 auto;
    max-width: 150px;
  }
.list{
  overflow: initial !important;
}


</style>



